<template>
    <a
        :href="link"
        :data-name="cardName"
        class="deck-card"
        target="_blank"
        @contextmenu="contextEvent"
    >
        <img
            :src="image"
            :alt="cardName"
            width="100"
            height="135"
        >
        <div class="deck-card-text">
            <span class="deck-card-name">{{ cardName || `[${cardId}]` }}</span>
            <slot
                class="deck-card-price"
                name="price"
            />
        </div>
    </a>
</template>

<script>
import getUrls from "../lib/data/urls";
import { isNil } from "lightdash";

const urls = getUrls();

export default {
    props: {
        cardId: {
            type: String,
            required: true
        },
        cardName: {
            type: String,
            required: false,
            default: null
        }
    },
    computed: {
        hasData() {
            return !isNil(this.cardName);
        },
        image() {
            return this.hasData
                ? `${urls.imageAPI}/${this.cardId}.jpg`
                : urls.imageUnkown;
        },
        link() {
            return this.hasData
                ? urls.dbAPI + encodeURI(this.cardName)
                : urls.wikiAPI + this.cardId;
        }
    },
    methods: {
        contextEvent(e) {
            this.$emit("deckcardrightclick", e);
        }
    }
};
</script>

